.content {
    flex: 2;
    padding-right: 30px;
    @include md {
        padding-right: 0;
    }
    @include xxl {
        order: 2;
    }
    .types {
        &__item {
            padding: 5px;
            display: flex;
            align-items: center;
            border: 2px solid var(--accent-color);
            border-radius: 50px;
            &:not(:last-child) {
                margin-bottom: 15px;
            }
            &:last-child {
                margin-bottom: 20px;
            }
            @include md {
                padding: 0 5px 0 0;
            }
        }
        &__link {
            min-width: 320px;
            max-width: 320px;
            padding: 25px 25px;
            text-transform: uppercase;
            font-weight: 600;
            border-radius: 50px;
            text-align: center;
            border: 2px solid var(--accent-color);
            transition: background 200ms, color 200ms;
            &:hover {
                background-color: var(--accent-color);
                color: var(--light-color);
            }
            @include xxl {
                max-width: 100%;
                width: 100%;
                min-width: 100px;
            }
            @include md {
                font-size: 14px;
                font-weight: 500;
                padding: 10px 10px;
            }
        }
        &__info {
            padding: 0 20px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @include md {
                padding: 0 5px;
            }
        }
        &__detail {
            padding-left: 30px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
            &-text {
                margin: 0 auto;
                text-align: center;
                font-weight: 500;
                @include md {
                    font-size: 14px;
                }
            }
            @include md {
                padding-left: 0;
                gap: 0;
            }
            &-icons {
                @include md {
                    display: none;
                }
            }
        }
        &__icon {
            height: 50px;
            @include md {
                display: none;
            }
        }
    }
}
.content.content--test {
    display: flex;
    @include md {
        flex-direction: column;
    }
    .types {
        min-width: 320px;
        max-width: 320px;
        @include xxl {
            max-width: 100%;
            width: 100%;
            min-width: 100px;
        }
        &__item {
            @include md  {
                padding: 0;
            }
        }
        &__link {
            min-width: auto;
        }
    }
    .content__quiz,
    .quiz {
        width: 100%;
        margin-left: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        @include md {
            margin-left: 0;
            margin-top: 10px;
        }
        &__wrapper {
            width: 100%;
            height: 100%;
            border: 4px solid var(--accent-color);
            border-radius: 10px;
            padding: 20px;
        }
        &__start {
            border: none;
            text-align: center;
            border-radius: 50px;
            cursor: pointer;
            display: inline-block;
            background-color: var(--accent-color);
            color: var(--light-color);
            font-weight: 500;
            padding: 20px 24px;
            min-width: 220px;
            text-transform: uppercase;
        }
        &__header {
            display: flex;
            justify-content: center;
            margin-bottom: 5px;
        }
        &__title {
            margin: 0;
            text-align: center;
            font-size: 28px;
            font-weight: 600;
        }
        &__time {
            font-weight: 500;
            margin-bottom: 10px;
            padding-left: 10px;
            display: inline-block;
        }
        &__window {
            background-color: rgba(198, 156, 109, 0.5);
            padding: 10px;
            border-radius: 10px;
        }
        &__question {
            width: 100%;
            padding: 15px 15px;
            border-radius: 10px;
            border: 3px solid var(--accent-color);
            background-color: var(--light-color);
            &-value {
                margin: 0;
                font-size: 20px;
                font-weight: 600;
            }
        }
        &__options {
            padding-top: 20px;
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
        &__group {
            width: 100%;
        }
        &__option {
            padding: 13px 20px;
            border-radius: 5px;
            background-color: rgba(255,255,255,.5);
            border: 2px solid transparent;
            cursor: pointer;
            transition: all 200ms ;
        }
        &__input {
            display: none;
        }
        &__text {
            font-size: 16px;
            font-weight: 600;
        }
        &__button {
            border: none;
            text-align: center;
            border-radius: 10px;
            cursor: pointer;
            margin-left: auto;
            display: inline-flex;
            background-color: var(--accent-color);
            color: var(--light-color);
            font-weight: 600;
            padding: 15px 20px;
            text-transform: uppercase;
            @include md {
                margin: 0;
                width: 100%;
                text-align: center;
                display: block;
            }
        }
        &__info {
            padding: 10px 10px 0 10px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}
.quiz__input:checked + .quiz__option {
    background-color: var(--light-color);
    border: 2px solid var(--black-color);
}
.content.content--video {
    display: flex;
    padding-right: 0px;
    @include md {
        flex-direction: column;
    }
    .types {
        min-width: 320px;
        max-width: 320px;
        @include xxl {
            max-width: 100%;
            width: 100%;
            min-width: 100px;
        }
        &__item {
            @include md  {
                padding: 0;
            }
        }
        &__link {
            min-width: auto;
        }
    }
    .content__video,
    .video {
        width: 100%;
        @include md {
            margin-left: 0;
            margin-top: 10px;
        }
        &__wrapper {
            width: 100%;
            padding: 0 30px;
            @include md {
                padding: 0px;
            }
        }
        &__window {
            width: 100%;
        }
        &__preview {
            width: 100%;
        }
        &__tools {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 20px;
            padding-top: 20px;
        }
        &__tool {
            &-link {
                text-align: center;
                border-radius: 50px;
                cursor: pointer;
                display: inline-block;
                background-color: var(--accent-color);
                color: var(--light-color);
                font-weight: 500;
                padding: 20px 24px;
                min-width: 160px;
                text-transform: uppercase;
                @include md {
                    font-size: 14px;
                    padding: 10px 15px;
                    min-width: auto;
                }
            }
        }
    }
}