.headline {
    width: 100%;
    padding: 20px 0;
    background-color: var(--accent-color);
    &__content {
        display: flex;
        align-items: center;
        min-height: 65px;
        @include md {
            flex-direction: column;
        }
    }
    &__title {
        margin: 0;
        color: #fff;
        opacity: .7;
        text-transform: uppercase;
        font-size: 36px;
        font-weight: 700;
        @include md {
            font-size: 24px;
        }
        // flex: 1;
    }
    &__theme {
        margin: 0;
        color: #fff;
        text-align: center;
        font-size: 28px;
        font-weight: 600;
        max-width: 760px;
        text-transform: uppercase;
        margin: 0 auto;
        @include md {
            font-size: 18px;
        }
        // flex: 2;
    }
}