.header {
    width: 100%;
    padding: 10px 0;
    &__content {
        display: flex;
        align-items: center;
        @include xxl {
            flex-direction: column;
            gap: 10px;
        }
    }
    &__col {
        --width: 320px;
        @include xxl {
            --width: 100%;
        }
        &--left {
            max-width: var(--width);
            @include xxl {
                order: 2;
            }
        }
        &--center {
            width: calc(100% - (var(--width) * 2));
            display: flex;
            justify-content: center;
            @include xxl {
                width: 100%;
                order: 1;
            }
        }
        &--right {
            max-width: var(--width);
            @include xxl {
                max-width: 100%;
                order: 3;
            }
        }
    }
    &__list {
        display: flex;
        align-items: center;
        gap: 20px;
        &-button {
            text-transform: uppercase;
            font-weight: 500;
            transition: color 200ms;
            &:hover {
                color: var(--accent-color);
            }
        }
    }
    &__logo {
        &-icon {
            height: 60px;
        }
    }
    &__title {
        text-transform: uppercase;
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        margin: 0;
    }
}