    // .main-wrapper__content-center

    &__content-center {
    }
}
.content {

    // .content__types

    &__types {
    }

    // .content__link

    &__link {
    }
}
.types {

    // .types__item

    &__item {
        @include xl{
            display: flex !important;
            padding: 24px !important;
        }
        @include sm{
            display: flex !important;
            flex-direction: column !important;
            gap: 30px !important;
            max-height: 600px !important;
        }
        
    }

    // .types__link

    &__link {
        @include xl{
            width: 400px !important;
            font-size: 18px !important;
        }
        @include sm{
            font-size: 16px !important;
        }
    }

    // .types__info

    &__info {
    }

    // .types__icon

    &__icon {
        @include xl{
            display: none !important;
        }
    }

    // .types__detail

    &__detail {
        @include sm{
            flex-direction: column !important;
        }
    }

    // .types__detail-text

    &__detail-text {
        @include xl{
            font-size: 20px !important;
        }
        @include sm{
            font-size: 16px !important;
        }
    }

    // .types__detail-icons

    &__detail-icons {

        @include sm{
            display: flex !important;
            margin-top: 30px !important;
        }
    }

    // .types__detail-icon

    &__detail-icon {
        width: 50px !important; 
        height: 50px !important;
        @include xl{
            margin-bottom: 20px !important;
        }
        @include sm{
            width: 40px !important; 
            height: 40px !important;
        }
    }

    // .types__detail-link

    &__detail-link {
    }

    // .types__detail-img

    &__detail-img {
        width: 40px !important; 
        height: 40px !important;
        @include xl{
            width: 50px !important; 
            height: 50px !important;
        }
        @include sm{
            width: 40px !important; 
            height: 40px !important;
        }
    }
}
