.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    background-color: rgb(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(1px);
    -moz-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
    &__wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__window {
        position: relative;
        min-width: 380px;
        min-height: 280px;
        border-radius: 20px;
        border: 4px solid var(--accent-color);
        background-color: var(--light-color);
        @include md {
            min-width: calc(100% - 20px);
            width: calc(100% - 20px);
            max-width: calc(100% - 20px);
        }
        &-close {
            position: absolute;
            top: 0;
            right: 0;
            border: none;
            background-color: rgb(255, 30, 0);
            cursor: pointer;
            width: 30px;
            height: 30px;
            font-size: 18px;
            color: var(--light-color);
            border-radius: 5px;
            font-weight: 600;
            transform: translate(50%,-50%);
            @include md {
                transform: translate(0,0);
            }
        }
        &-header {
            padding: 20px;
            width: 100%;
            display: flex;
            justify-content: center;
        }
        &-title {
            margin: 0;
            text-align: center;
            font-size: 18px;
            font-weight: 500;
        }
        &-body {
            padding: 0 20px 20px 20px;
        }
        &-text {
            margin: 0;
            text-align: left;
            font-weight: 500;
        }
    }
}
.modal.modal--success {
    .modal__window {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
    }
    .modal__window-icon {
        height: 80px;
    }
    .modal__window-title {
        font-weight: 600;
        font-size: 22px;
        text-align: center;
    }
    .modal__window-status {
        margin: 0;
    }
}