.sidebar-right {
    max-width: 320px;
    @include xxl {
        width: 100%;
        order: 1;
    }
    &__list {
        display: flex;
        align-items: start;
        flex-direction: column;
        gap: 20px;
        @include xxl {
            flex-direction: row;
            padding-bottom: 20px;
        }
        @include md {
            flex-direction: column;
            gap: 10px;
        }
    }
    .logo {
        &__icon {
           max-width: 100px;
        }
    }
    .group-info {
        &__title {
            margin: 0 0 10px 0;
            color: var(--accent-color);
            font-weight: 600;
            font-size: 22px;
            text-align: left;
            text-transform: uppercase;
            @include md {
                font-size: 16px;
                margin: 0;
            }
        }
        &__subtitle {
            margin: 0;
            font-weight: 500;
            font-size: 16px;
            text-align: left;
            text-transform: uppercase;
            @include md {
                font-size: 14px;
            }
        }
    }
}